import { ExampleDescription, loadPrefixedStylesheet } from "@smartdesign/element-preview";

const example: ExampleDescription = {
    mainContent: require("./example.html?raw"),
    description: require("./example.md").default as string,
    css: require("!raw-loader!./example.css").default,
    initializer: {
        content: require("!raw-loader!./example.ts").default,
        type: "typescript",
        initialize: () => {
            // example.ts is not executed as it's not valid in the documentation context
            const theme = require("!raw-loader!sass-loader!import-glob-loader!../../../scss/theme.scss").default;
            loadPrefixedStylesheet("#theme-example", theme);
        },
    },
};

export default example;
